
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import Tab from 'components/tab/tab'
import ColumnCell from 'components/column-cell/column-cell'
import { getFavoriteColumn, deleteFavoriteColumn } from './api'
import { ERR_OK } from 'common/api/config'
import { mapGetters } from 'vuex'
import storage from 'good-storage'

export default {
name: 'favorite',
props: {},
data () {
return {
columns: [],
userId: ''
}
},
computed: {
...mapGetters([
'userInfo'
])
},
components: {
Tab,
ColumnCell,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
watch: {},
methods: {
async _getFavoriteColumn () {
const params = {}
return await getFavoriteColumn(params, this.userId).then(data => {
if (data.code === ERR_OK) {
this.columns = data.columns
return data
}
})
},
async _deleteFavoriteColumn (columnId, userId) {
const params = {}
userId = this.userId
return await deleteFavoriteColumn(params, columnId, this.userId).then(data => {
if (data.code === ERR_OK) {
this._getFavoriteColumn(userId)
return data
}
})
}
},
created () {
this.userId = storage.get('aiwei').userInfo._id
this._getFavoriteColumn()
},
mounted () {}
}



